<script>
  /**
   * üó∫Ô∏è MapTab.vue - Âú∞ÂúñÈ°ØÁ§∫ÁµÑ‰ª∂ (Map Display Component)
   *
   * ÈÄôÊòØÊáâÁî®Á®ãÂºèÁöÑÊ†∏ÂøÉÂú∞ÂúñÁµÑ‰ª∂ÔºåË≤†Ë≤¨Ê∏≤Êüì‰∏ñÁïåÂú∞Âúñ„ÄÅËôïÁêÜÁî®Êà∂‰∫íÂãïÂíåÊèê‰æõÂ∞éËà™ÂäüËÉΩ„ÄÇ
   * Êú¨ÁµÑ‰ª∂Êï¥Âêà‰∫Ü Leaflet Âú∞ÂúñÂ∫´„ÄÅPinia ÁãÄÊÖãÁÆ°ÁêÜÂíå Vue 3 ÈüøÊáâÂºèÁ≥ªÁµ±Ôºå
   * Êèê‰æõÊµÅÊö¢ÁöÑÂú∞ÂúñÁÄèË¶ΩÈ´îÈ©óÂíåË±êÂØåÁöÑ‰∫íÂãïÂäüËÉΩ„ÄÇ
   *
   * üéØ Ê†∏ÂøÉÂäüËÉΩÔºö
   * - üó∫Ô∏è Âú∞ÂúñÊ∏≤ÊüìÔºö‰ΩøÁî® Leaflet Ê∏≤Êüì‰∏ñÁïåÂú∞ÂúñÂíåË°õÊòüÂúñÂÉè
   * - üåç ÂúãÂÆ∂Â∞éËà™ÔºöÊèê‰æõÂÖ≠Â§ßÂúãÂÆ∂ÁöÑÂø´ÈÄüÂ∞éËà™ÂäüËÉΩ
   * - üéõÔ∏è ‰∫íÂãïÊéßÂà∂ÔºöËôïÁêÜÂú∞ÂúñÁ∏ÆÊîæ„ÄÅÊãñÊãΩÂíåË¶ñÂúñËÆäÊõ¥
   * - üì± ÈüøÊáâÂºèÈÅ©ÈÖçÔºöËá™ÂãïÈÅ©Êáâ‰∏çÂêåË®≠ÂÇôÁöÑËû¢ÂπïÂ∞∫ÂØ∏
   * - üé® Ë¶ñË¶∫ÊïàÊûúÔºöÊèê‰æõÂπ≥ÊªëÁöÑÂãïÁï´ÂíåÈÅéÊ∏°ÊïàÊûú
   * - üîÑ ÁãÄÊÖãÂêåÊ≠•ÔºöËàá Pinia ÁãÄÊÖãÁÆ°ÁêÜÁ≥ªÁµ±‰øùÊåÅÂêåÊ≠•
   *
   * üèóÔ∏è ÁµÑ‰ª∂Êû∂ÊßãÔºö
   * - Âú∞ÂúñÂÆπÂô®ÔºöÊèê‰æõÂú∞ÂúñÊ∏≤ÊüìÁöÑ DOM ÂÆπÂô®
   * - ÊéßÂà∂Èù¢ÊùøÔºöÂúãÂÆ∂Â∞éËà™ÊåâÈàïÂíåÁî®Êà∂ÊéßÂà∂
   * - Âú∞ÂúñÂØ¶‰æãÔºöLeaflet Âú∞ÂúñÂ∞çË±°ÂíåÈÖçÁΩÆ
   * - ‰∫ã‰ª∂ËôïÁêÜÔºöÁî®Êà∂‰∫íÂãïÂíåÂú∞Âúñ‰∫ã‰ª∂Áõ£ËÅΩ
   * - ÁãÄÊÖãÁÆ°ÁêÜÔºöËàá Pinia Store ÁöÑÊï∏ÊìöÂêåÊ≠•
   *
   * üîß ÊäÄË°ìÊï¥ÂêàÔºö
   * - Leaflet 1.9+ÔºöÈñãÊ∫êÂú∞ÂúñÂ∫´ÔºåÊèê‰æõÂú∞ÂúñÊ∏≤ÊüìÂíå‰∫íÂãï
   * - Vue 3 Composition APIÔºöÁèæ‰ª£ÂåñÁöÑÁµÑ‰ª∂ÈñãÁôºÊ®°Âºè
   * - Pinia ÁãÄÊÖãÁÆ°ÁêÜÔºöÈüøÊáâÂºèÊï∏ÊìöÁÆ°ÁêÜÂíåÁãÄÊÖãÂêåÊ≠•
   * - Bootstrap 5ÔºöÈüøÊáâÂºè UI Ê°ÜÊû∂ÂíåÊ®£ÂºèÁ≥ªÁµ±
   * - ResizeObserverÔºöÂÆπÂô®Â∞∫ÂØ∏ËÆäÊõ¥Áõ£ËÅΩÂíåÂú∞ÂúñÂà∑Êñ∞
   *
   * üìä Êï∏ÊìöÊµÅÔºö
   * 1. ÁµÑ‰ª∂ÊéõËºâ ‚Üí ÂàùÂßãÂåñÂú∞ÂúñÂØ¶‰æã
   * 2. Âú∞ÂúñÊ∫ñÂÇô ‚Üí Ë®≠ÂÆöÂ∫ïÂúñÂíåÈÖçÁΩÆ
   * 3. Áî®Êà∂Êìç‰Ωú ‚Üí Ëß∏ÁôºÂ∞éËà™‰∫ã‰ª∂
   * 4. ÁãÄÊÖãÊõ¥Êñ∞ ‚Üí ÈüøÊáâÂºèÈáçÊñ∞Ê∏≤Êüì
   * 5. Âú∞ÂúñÂêåÊ≠• ‚Üí Êõ¥Êñ∞Ë¶ñÂúñÂíå‰ΩçÁΩÆ
   *
   * üé® Ë¶ñË¶∫Ë®≠Ë®àÔºö
   * - Google Ë°õÊòüÂ∫ïÂúñÔºöÊèê‰æõÁúüÂØ¶ÁöÑÂú∞ÁêÜË¶ñË¶∫ÊïàÊûú
   * - Ê∑±Ëâ≤ÊéßÂà∂Èù¢ÊùøÔºöÂçäÈÄèÊòéËÉåÊôØÔºåÁ™ÅÂá∫Âú∞ÂúñÂÖßÂÆπ
   * - ÁôΩËâ≤‰∏≠ÂøÉÈªûÔºöÂõ∫ÂÆöÊ®ôË®òÔºå‰æøÊñºË¶ñË¶∫ÂÆö‰Ωç
   * - Âπ≥ÊªëÂãïÁï´ÔºöÂú∞ÂúñÁßªÂãïÂíåË¶ñÂúñËÆäÊõ¥ÁöÑÊµÅÊö¢ÈÅéÊ∏°
   *
   * üöÄ ÊÄßËÉΩÂÑ™ÂåñÔºö
   * - Âª∂ÈÅ≤ÂàùÂßãÂåñÔºöÁ≠âÂæÖÂÆπÂô®Ê∫ñÂÇôÂ∞±Á∑íÂæåÂâµÂª∫Âú∞Âúñ
   * - ÈáçË©¶Ê©üÂà∂ÔºöÂú∞ÂúñÂâµÂª∫Â§±ÊïóÊôÇËá™ÂãïÈáçË©¶
   * - Â∞∫ÂØ∏Áõ£ËÅΩÔºö‰ΩøÁî® ResizeObserver ÂÑ™ÂåñÂÆπÂô®ËÆäÊõ¥ËôïÁêÜ
   * - ‰∫ã‰ª∂Èò≤ÊäñÔºöÈÅøÂÖçÈ†ªÁπÅÁöÑÂú∞ÂúñÊõ¥Êñ∞Êìç‰Ωú
   *
   * @author 30 Day Map Challenge Team
   * @version 1.0.0
   * @since 2024
   */

  import { ref, onMounted, onUnmounted, watch, nextTick, computed } from 'vue';
  import L from 'leaflet';
  import 'leaflet/dist/leaflet.css';
  import { useDataStore } from '@/stores/dataStore.js';
  import { useDefineStore } from '@/stores/defineStore.js';

  export default {
    name: 'MapTab',
    props: {},
    emits: ['map-ready'],
    setup(props, { emit }) {
      // üì¶ Â≠òÂÑ≤ÂØ¶‰æã
      const dataStore = useDataStore();
      const defineStore = useDefineStore();

      // üó∫Ô∏è Âú∞ÂúñÁõ∏ÈóúËÆäÊï∏
      const mapContainer = ref(null);
      let mapInstance = null;
      let currentTileLayer = null;

      // üéõÔ∏è Âú∞ÂúñÊéßÂà∂ÁãÄÊÖã
      const isMapReady = ref(false);
      const mapContainerId = ref(`leaflet-map-${Math.random().toString(36).substr(2, 9)}`);
      const selectedCountryId = ref('taiwan'); // È†êË®≠ÈÅ∏‰∏≠Âè∞ÁÅ£

      // üìä Ë®àÁÆóÂ±¨ÊÄßÔºöÊ™¢Êü•ÊòØÂê¶Êúâ‰ªª‰ΩïÂúñÂ±§ÂèØË¶ã
      const isAnyLayerVisible = computed(() => dataStore.getAllLayers().length > 0);

      /**
       * üåç Â∞éËà™Âà∞ÊåáÂÆöÂúãÂÆ∂
       * @param {string} countryId - ÂúãÂÆ∂ ID
       */
      const navigateToCountry = (countryId) => {
        selectedCountryId.value = countryId; // Êõ¥Êñ∞ÈÅ∏‰∏≠ÁãÄÊÖã
        dataStore.navigateToCountry(countryId);
      };

      // üìä Ë®àÁÆóÂ±¨ÊÄßÔºöÁç≤ÂèñÊâÄÊúâÂúãÂÆ∂ÂúñÂ±§
      const countries = computed(() => dataStore.getAllLayers());

      /**
       * üèóÔ∏è ÂâµÂª∫Âú∞ÂúñÂØ¶‰æã
       * ÂàùÂßãÂåñ Leaflet Âú∞Âúñ‰∏¶Ë®≠ÂÆöÂü∫Êú¨ÈÖçÁΩÆ
       */
      const createMap = () => {
        if (!mapContainer.value) return false;

        const rect = mapContainer.value.getBoundingClientRect();
        if (rect.width === 0 || rect.height === 0) {
          console.warn('[MapTab] ÂÆπÂô®Â∞∫ÂØ∏ÁÇ∫Èõ∂ÔºåÂª∂ÈÅ≤ÂàùÂßãÂåñ');
          return false;
        }

        try {
          // Âæû layer Êï∏Êìö‰∏≠ËÆÄÂèñÂè∞ÁÅ£ÁöÑÂ∫ßÊ®ô
          const taiwanLayer = dataStore.findLayerById('taiwan');
          const [lat, lng] = taiwanLayer ? taiwanLayer.center : [25.0457, 121.5196];

          mapInstance = L.map(mapContainer.value, {
            center: [lat, lng], // Âæû layer Êï∏ÊìöËÆÄÂèñÂè∞ÁÅ£Â∫ßÊ®ô
            zoom: dataStore.FIXED_ZOOM_LEVEL,
            zoomControl: false, // Á¶ÅÁî®Á∏ÆÊîæÊéßÂà∂
            attributionControl: false,
            dragging: true, // ÂïüÁî®ÊãñÊãΩ
            touchZoom: false, // Á¶ÅÁî®Ëß∏ÊéßÁ∏ÆÊîæ
            doubleClickZoom: false, // Á¶ÅÁî®ÈõôÊìäÁ∏ÆÊîæ
            scrollWheelZoom: false, // Á¶ÅÁî®ÊªæËº™Á∏ÆÊîæ
            boxZoom: false, // Á¶ÅÁî®Ê°ÜÈÅ∏Á∏ÆÊîæ
            keyboard: false, // Á¶ÅÁî®ÈçµÁõ§ÊéßÂà∂
            zoomSnap: 0, // Á¶ÅÁî®Á∏ÆÊîæÂê∏ÈôÑ
            zoomDelta: 0, // Á¶ÅÁî®Á∏ÆÊîæÂ¢ûÈáè
          });

          // Á∂ÅÂÆöÂú∞Âúñ‰∫ã‰ª∂
          mapInstance.on('zoomend', handleZoomEnd);
          mapInstance.on('moveend', handleMoveEnd);

          // ÁßªÈô§Âú∞ÂúñÈªûÊìä‰∫ã‰ª∂ËôïÁêÜ

          // Ë®≠ÂÆöÂúñÂ±§ z-index È†ÜÂ∫èÔºàÂæûÂ∫ïÂ±§Âà∞È†ÇÂ±§Ôºâ
          // 1. Â∫ïÂúñÔºà‰∏ñÁïåÂú∞ÂúñÔºâ- ÊúÄÂ∫ïÂ±§
          if (mapInstance.getPane('tilePane')) {
            mapInstance.getPane('tilePane').style.zIndex = 1000;
          }

          // 2. Ê®ôË®òÂúñÂ±§
          if (mapInstance.getPane('markerPane')) {
            mapInstance.getPane('markerPane').style.zIndex = 2000;
          }

          // 3. Ë¶ÜËìãÂúñÂ±§Ôºàheatmap Á≠âÔºâ
          if (mapInstance.getPane('overlayPane')) {
            mapInstance.getPane('overlayPane').style.zIndex = 2100;
          }

          // 4. ÂΩàÂá∫Ë¶ñÁ™ó - ÊúÄÈ†ÇÂ±§
          mapInstance.getPane('popupPane').style.zIndex = 2200;

          isMapReady.value = true;
          emit('map-ready', mapInstance);

          return true;
        } catch (error) {
          console.error('[MapTab] Âú∞ÂúñÂâµÂª∫Â§±Êïó:', error);
          return false;
        }
      };

      /**
       * üì° ËôïÁêÜÁ∏ÆÊîæÁµêÊùü‰∫ã‰ª∂
       * Êõ¥Êñ∞Âú∞ÂúñË¶ñÂúñÁãÄÊÖãÂà∞Â≠òÂÑ≤‰∏≠
       */
      const handleZoomEnd = () => {
        if (mapInstance) {
          const zoom = mapInstance.getZoom();
          emit('update:zoomLevel', zoom);
        }
      };

      /**
       * üì° ËôïÁêÜÁßªÂãïÁµêÊùü‰∫ã‰ª∂
       * Êõ¥Êñ∞Âú∞Âúñ‰∏≠ÂøÉÂ∫ßÊ®ô
       */
      const handleMoveEnd = () => {
        if (mapInstance) {
          const center = mapInstance.getCenter();
          emit('update:currentCoords', { lat: center.lat, lng: center.lng });
        }
      };

      /**
       * üé® Ë®≠ÂÆöÂ∫ïÂúñ
       * Ê†πÊìöÂ≠òÂÑ≤‰∏≠ÁöÑË®≠ÂÆöËºâÂÖ•Â∞çÊáâÁöÑÂ∫ïÂúñÂúñÂ±§
       */
      const setBasemap = () => {
        if (!mapInstance) return;

        // ÁßªÈô§ÁèæÊúâÂ∫ïÂúñ
        if (currentTileLayer) {
          mapInstance.removeLayer(currentTileLayer);
        }

        const config = defineStore.basemaps.find((b) => b.value === defineStore.selectedBasemap);

        // Ê∑ªÂä†Â∫ïÂúñÂúñÂ±§
        if (config && config.url) {
          currentTileLayer = L.tileLayer(config.url, {
            attribution: '¬© Google',
            maxZoom: 20,
            subdomains: ['mt0', 'mt1', 'mt2', 'mt3'],
          });
          mapInstance.addLayer(currentTileLayer);
        }

        // ‰ΩøÁî®È†êË®≠ÁöÑÈÄèÊòéËÉåÊôØÔºå‰∏çË®≠ÂÆö‰ªª‰ΩïÁâπÊÆäËÉåÊôØËâ≤
      };

      // ÁßªÈô§Êú™‰ΩøÁî®ÁöÑÈ´ò‰∫ÆÂäüËÉΩÂáΩÊï∏

      /**
       * üîÑ ÂêåÊ≠•ÂúñÂ±§ÔºàÂ∑≤ÁßªÈô§Ê®ôË®òÂäüËÉΩÔºâ
       * ‰∏çÂÜçÂú®Âú∞Âúñ‰∏äÂâµÂª∫Ê®ôË®òÔºåÊîπÁÇ∫Âú® HTML ‰∏äÈ°ØÁ§∫
       */
      const syncLayers = () => {
        // ÁßªÈô§Âú∞ÂúñÊ®ôË®òÂäüËÉΩÔºå‰∏çÈúÄË¶ÅÂêåÊ≠•‰ªª‰ΩïÂúñÂ±§
      };

      /**
       * üìè Âà∑Êñ∞Âú∞ÂúñÂ∞∫ÂØ∏
       * Áï∂ÂÆπÂô®Â§ßÂ∞èÊîπËÆäÊôÇÈáçÊñ∞Ë®àÁÆóÂú∞ÂúñÂ∞∫ÂØ∏
       */
      const invalidateSize = () => {
        if (mapInstance) {
          setTimeout(() => {
            mapInstance.invalidateSize();
          }, 100);
        }
      };

      /**
       * üöÄ ÂàùÂßãÂåñÂú∞Âúñ
       * ÂâµÂª∫Âú∞Âúñ‰∏¶ËºâÂÖ•ÂàùÂßãÊï∏Êìö
       */
      const initMap = () => {
        let attempts = 0;
        const maxAttempts = 20;

        const tryCreateMap = () => {
          if (attempts >= maxAttempts) {
            console.error('[MapTab] Âú∞ÂúñÂàùÂßãÂåñÂ§±ÊïóÔºåÂ∑≤ÈÅîÂà∞ÊúÄÂ§ßÂòóË©¶Ê¨°Êï∏');
            return;
          }

          attempts++;
          console.log(`[MapTab] ÂòóË©¶ÂâµÂª∫Âú∞Âúñ (${attempts}/${maxAttempts})`);

          if (createMap()) {
            setBasemap();
            syncLayers();
            // È†êË®≠Â∞éËà™Âà∞Âè∞ÁÅ£
            setTimeout(() => {
              navigateToCountry('taiwan');
            }, 500);
          } else {
            setTimeout(tryCreateMap, 100);
          }
        };

        tryCreateMap();
      };

      // üìè Ë®≠ÁΩÆ ResizeObserver Áõ£ËÅΩÂÆπÂô®Â§ßÂ∞èËÆäÂåñ
      let resizeObserver = null;
      let resizeTimeout = null;

      const setupResizeObserver = () => {
        if (!mapContainer.value || !window.ResizeObserver) return;

        resizeObserver = new ResizeObserver(() => {
          if (resizeTimeout) {
            clearTimeout(resizeTimeout);
          }

          resizeTimeout = setTimeout(() => {
            invalidateSize();
          }, 200);
        });

        resizeObserver.observe(mapContainer.value);
      };

      // üßπ ÁîüÂëΩÈÄ±ÊúüÔºöÁµÑ‰ª∂ÊéõËºâ
      onMounted(() => {
        nextTick(() => {
          initMap();
          setupResizeObserver();
        });
      });

      // üßπ ÁîüÂëΩÈÄ±ÊúüÔºöÁµÑ‰ª∂Âç∏Ëºâ
      onUnmounted(() => {
        if (resizeTimeout) {
          clearTimeout(resizeTimeout);
        }

        if (resizeObserver) {
          resizeObserver.disconnect();
        }

        if (mapInstance) {
          mapInstance.remove();
          mapInstance = null;
        }

        currentTileLayer = null;
        isMapReady.value = false;
      });

      // üëÄ Áõ£ËÅΩÂô®ÔºöÁõ£ËÅΩË≥áÊñôÂ≠òÂÑ≤‰∏≠ÁöÑÂúñÂ±§ËÆäÂåñ
      watch(() => dataStore.layers, syncLayers, { deep: true });

      // üëÄ Áõ£ËÅΩÂô®ÔºöÁõ£ËÅΩÂ∫ïÂúñËÆäÂåñ
      watch(
        () => defineStore.selectedBasemap,
        () => {
          if (isMapReady.value) {
            setBasemap();
          }
        }
      );

      // üì§ ËøîÂõûÁµÑ‰ª∂ÂÖ¨ÈñãÁöÑÂ±¨ÊÄßÂíåÊñπÊ≥ï
      return {
        mapContainer,
        mapContainerId,
        isAnyLayerVisible,
        invalidateSize,
        defineStore,
        navigateToCountry,
        countries,
        selectedCountryId,
      };
    },
  };
</script>

<template>
  <!-- üó∫Ô∏è Âú∞Âúñ‰∏ªÂÆπÂô® -->
  <div id="map-container" class="h-100 w-100 position-relative bg-transparent z-0">
    <!-- üéõÔ∏è Â∑¶ÂÅ¥‰∏≠ÈñìÊéßÂà∂Èù¢Êùø -->
    <div
      class="position-absolute"
      style="top: 50%; left: 0; transform: translateY(-50%); z-index: 1000; padding: 1rem"
    >
      <div class="bg-dark bg-opacity-75 rounded-3 p-3">
        <!-- üåç ÂúãÂÆ∂Â∞éËà™ÂçÄÂüü -->
        <div class="">
          <div class="d-flex flex-column gap-1">
            <button
              v-for="country in countries"
              :key="country.layerId"
              type="button"
              class="btn border-0 my-country-btn my-font-sm-white px-4 py-3 text-center"
              :class="[selectedCountryId === country.layerId ? 'active' : '']"
              @click="navigateToCountry(country.layerId)"
            >
              {{ country.layerName }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- üéØ Âú∞Âúñ‰∏≠ÂøÉÈªûÊ®ôË®ò -->
    <div class="position-absolute map-center-point">
      <div class="center-dot"></div>
    </div>

    <!-- üó∫Ô∏è Leaflet Âú∞ÂúñÂÆπÂô® -->
    <div :id="mapContainerId" ref="mapContainer" class="h-100 w-100"></div>
  </div>
</template>

<style>
  @import '../assets/css/common.css';

  /* üéØ Âú∞Âúñ‰∏≠ÂøÉÈªûÊ®£Âºè */
  .map-center-point {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2000;
    pointer-events: none; /* ‰∏çÂΩ±ÈüøÂú∞Âúñ‰∫íÂãï */
  }

  .center-dot {
    width: 4px;
    height: 4px;
    background-color: red; /* Bootstrap Á¥ÖËâ≤ */
    border-radius: 50%;
  }
</style>
