export const useDataStore = defineStore(
  'data', // ÂïÜÂ∫óÂîØ‰∏ÄÊ®ôË≠òÁ¨¶ÔºåÁî®Êñº Pinia ÂÖßÈÉ®ÁÆ°ÁêÜÂíåÈñãÁôºÂ∑•ÂÖ∑Ë≠òÂà•
  () => {
    // üéØ Âú∞ÂúñÁ∏ÆÊîæÁ¥öÂà•Â∏∏Êï∏ (Map Zoom Level Constant)
    // Âõ∫ÂÆöÁ∏ÆÊîæÁ¥öÂà•ÁÇ∫ 17ÔºåÊèê‰æõÂüéÂ∏ÇÁ¥öÂà•ÁöÑË©≥Á¥∞Ë¶ñÂúñ
    // ÈÄôÂÄãÁ¥öÂà•ÈÅ©ÂêàÈ°ØÁ§∫ÂüéÂ∏ÇÂÖßÈÉ®ÁöÑË©≥Á¥∞Âú∞ÁêÜ‰ø°ÊÅØ
    const FIXED_ZOOM_LEVEL = 17;
    /**
     * üó∫Ô∏è ‰∏ñÁïåÂúãÂÆ∂ÂúñÂ±§ÈÖçÁΩÆÊï∏Êìö (World Countries Layer Configuration Data)
     *
     * ÈÄôÊòØÊáâÁî®Á®ãÂºèÁöÑÊ†∏ÂøÉÊï∏ÊìöÁµêÊßãÔºåÂÆöÁæ©‰∫Ü‰∏ñÁïåÂÖ≠Â§ßÂúãÂÆ∂ÁöÑÂú∞ÁêÜ‰ø°ÊÅØÂíåÈÖçÁΩÆ„ÄÇ
     * Êé°Áî®ÂàÜÁµÑÁµêÊßãÁµÑÁπîÔºåÊØèÂÄãÂúãÂÆ∂ÂåÖÂê´ÂÆåÊï¥ÁöÑÈÖçÁΩÆ‰ø°ÊÅØÔºå‰ΩøÁî® ref ÂâµÂª∫ÈüøÊáâÂºèÊï∏ÊìöÔºå
     * Áï∂ÂúñÂ±§ÁãÄÊÖãÊîπËÆäÊôÇÊúÉËá™ÂãïÊõ¥Êñ∞Áõ∏Èóú UI ÁµÑ‰ª∂„ÄÇ
     *
     * üèóÔ∏è Êï∏ÊìöÁµêÊßãË®≠Ë®àÔºö
     * - ÂàÜÁµÑÁÆ°ÁêÜÔºö‰ΩøÁî® groupName Âíå groupLayers ÂØ¶ÁèæÂ±§Á¥öÁµÑÁπî
     * - ÈüøÊáâÂºèÊõ¥Êñ∞Ôºö‰ΩøÁî® Vue 3 ÁöÑ ref ÂØ¶ÁèæËá™Âãï UI Êõ¥Êñ∞
     * - Âú∞ÁêÜÂ∫ßÊ®ôÔºöÁ≤æÁ¢∫ÁöÑÁ∂ìÁ∑ØÂ∫¶Â∫ßÊ®ôÔºåÊîØÊè¥Âú∞ÂúñÂ∞éËà™
     * - ÂúãÈöõÂåñÊîØÊè¥ÔºöÊîØÊè¥Â§öË™ûË®ÄÂúãÂÆ∂ÂêçÁ®±È°ØÁ§∫
     *
     * üìã ÂúñÂ±§ÁµêÊßãË©≥Á¥∞Ë™™ÊòéÔºö
     * - groupName: ÂúñÂ±§ÁµÑÂêçÁ®±ÔºåÁî®Êñº UI ÂàÜÁµÑÈ°ØÁ§∫ÂíåÈÇèËºØÁµÑÁπî
     * - groupLayers: Ë©≤ÁµÑ‰∏ãÁöÑÊâÄÊúâÂúñÂ±§ÂàóË°®ÔºåÊîØÊè¥ÂãïÊÖãÊ∑ªÂä†/ÁßªÈô§
     *   - layerId: ÂúñÂ±§ÂîØ‰∏ÄÊ®ôË≠òÁ¨¶ÔºåÁî®ÊñºÁ®ãÂºèÈÇèËºØË≠òÂà•ÂíåË∑ØÁî±
     *   - layerName: ÂúñÂ±§È°ØÁ§∫ÂêçÁ®±ÔºåÊîØÊè¥ÂúãÈöõÂåñÂíåÊú¨Âú∞Âåñ
     *   - center: ÂúãÂÆ∂‰∏≠ÂøÉÂ∫ßÊ®ô [Á∑ØÂ∫¶, Á∂ìÂ∫¶]ÔºåÁî®ÊñºÂú∞ÂúñÂ∞éËà™
     *   - zoom: Á∏ÆÊîæÁ¥öÂà•ÔºàÂèØÈÅ∏ÔºâÔºåÈ†êË®≠‰ΩøÁî® FIXED_ZOOM_LEVEL
     *
     * üåç ÊîØÊè¥ÁöÑÂúãÂÆ∂Ôºö
     * - Âè∞ÁÅ£ (Taiwan): ‰∫ûÊ¥≤Êù±ÈÉ®Â≥∂ÂúãÔºåÂ∫ßÊ®ô [25.0457, 121.5196]
     * - ‰∏≠Âúã (China): ‰∫ûÊ¥≤Â§ßÈô∏ÂúãÂÆ∂ÔºåÂ∫ßÊ®ô [39.89877, 116.39167]
     * - Êó•Êú¨ (Japan): ‰∫ûÊ¥≤Êù±ÈÉ®Â≥∂ÂúãÔºåÂ∫ßÊ®ô [35.684580490329374, 139.77440880399962]
     * - ÁæéÂúã (United States): ÂåóÁæéÊ¥≤ÂúãÂÆ∂ÔºåÂ∫ßÊ®ô [38.89511, -77.03655]
     * - Ê≥ïÂúã (France): Ê≠êÊ¥≤Ë•øÈÉ®ÂúãÂÆ∂ÔºåÂ∫ßÊ®ô [48.8534, 2.3488]
     * - Âæ∑Âúã (Germany): Ê≠êÊ¥≤‰∏≠ÈÉ®ÂúãÂÆ∂ÔºåÂ∫ßÊ®ô [52.5108, 13.399]
     *
     * üîÑ ÈüøÊáâÂºèÁâπÊÄßÔºö
     * - Ëá™ÂãïÊõ¥Êñ∞ÔºöÁï∂Êï∏ÊìöËÆäÊõ¥ÊôÇÔºåÊâÄÊúâ‰æùË≥¥ÁöÑ UI ÁµÑ‰ª∂ÊúÉËá™ÂãïÈáçÊñ∞Ê∏≤Êüì
     * - Ë®àÁÆóÂ±¨ÊÄßÔºöÊîØÊè¥Âü∫ÊñºÊ≠§Êï∏ÊìöÁöÑË®àÁÆóÂ±¨ÊÄßÔºåÂ¶Ç visibleLayers
     * - ÁãÄÊÖãÂêåÊ≠•ÔºöËàáÂú∞ÂúñÂØ¶‰æãÂíåÁî®Êà∂‰∫íÂãï‰øùÊåÅÂêåÊ≠•
     *
     * @type {Ref<Array>} ÈüøÊáâÂºèÂúñÂ±§ÈÖçÁΩÆÈô£Âàó
     * @since 1.0.0
     */
    // üåç ‰∏ñÁïåÂÖ≠Â§ßÂúãÂÆ∂Êï∏ÊìöÈÖçÁΩÆ (World Six Countries Data Configuration)
    const layers = ref([
      {
        groupName: '‰∏ñÁïåÂúãÂÆ∂',
        groupLayers: [
          {
            layerId: 'taiwan',
            layerName: 'TAIWAN',
            center: [25.0457, 121.5196],
          },
          {
            layerId: 'china',
            layerName: 'CHINA',
            center: [39.89877, 116.39167],
          },
          {
            layerId: 'japan',
            layerName: 'JAPAN',
            center: [35.684580490329374, 139.77440880399962],
          },
          {
            layerId: 'usa',
            layerName: 'UNITED STATES',
            center: [38.89511, -77.03655],
          },
          {
            layerId: 'france',
            layerName: 'FRANCE',
            center: [48.8534, 2.3488],
          },
          {
            layerId: 'germany',
            layerName: 'GERMANY',
            center: [52.5108, 13.399],
          },
        ],
      },
    ]);

    /**
     * üîç Ê†πÊìöÂúñÂ±§ ID Êü•ÊâæÂúñÂ±§Â∞çË±° (Find Layer by ID)
     *
     * ÈÄôÊòØ‰∏ÄÂÄãÊ†∏ÂøÉÁöÑÊï∏ÊìöÊü•Ë©¢ÊñπÊ≥ïÔºåÂú®ÂàÜÁµÑÁµêÊßãÁöÑÂúñÂ±§ÈÖçÁΩÆ‰∏≠ÊêúÁ¥¢ÊåáÂÆö ID ÁöÑÂúñÂ±§Ôºå
     * ËøîÂõûÂÆåÊï¥ÁöÑÂúñÂ±§ÈÖçÁΩÆÂ∞çË±°„ÄÇ‰ΩøÁî®È´òÊïàÁöÑÂµåÂ•óÂæ™Áí∞ÈÅçÊ≠∑ÁÆóÊ≥ïÔºåÁ¢∫‰øùËÉΩÂ§†Âø´ÈÄü
     * ÊâæÂà∞Ê≠£Á¢∫ÁöÑÂúñÂ±§ÔºåÊîØÊè¥ O(n*m) ÊôÇÈñìË§áÈõúÂ∫¶ÁöÑÊêúÁ¥¢„ÄÇ
     *
     * üéØ ÂäüËÉΩÁâπÊÄßÔºö
     * - Á≤æÁ¢∫ÂåπÈÖçÔºö‰ΩøÁî®Âö¥Ê†ºÁõ∏Á≠âÊØîËºÉÁ¢∫‰øù ID ÂåπÈÖçÁöÑÊ∫ñÁ¢∫ÊÄß
     * - ÂÆåÊï¥ËøîÂõûÔºöËøîÂõûÂÆåÊï¥ÁöÑÂúñÂ±§ÈÖçÁΩÆÂ∞çË±°ÔºåÂåÖÂê´ÊâÄÊúâÂ±¨ÊÄßÂíåÊñπÊ≥ï
     * - ÈåØË™§ËôïÁêÜÔºöÊú™ÊâæÂà∞ÊôÇËøîÂõû nullÔºå‰æøÊñºÂæåÁ∫åÁöÑÊ¢ù‰ª∂Âà§Êñ∑
     * - ÊÄßËÉΩÂÑ™ÂåñÔºö‰ΩøÁî® for...of Âæ™Áí∞ÔºåÊØîÂÇ≥Áµ± for Âæ™Áí∞Êõ¥È´òÊïà
     *
     * üîç ÊêúÁ¥¢ÁÆóÊ≥ïÔºö
     * 1. Â§ñÂ±§Âæ™Áí∞ÔºöÈÅçÊ≠∑ÊâÄÊúâÂúñÂ±§ÁµÑ (groups)
     * 2. ÂÖßÂ±§Âæ™Áí∞ÔºöÈÅçÊ≠∑Áï∂ÂâçÁµÑÁöÑÊâÄÊúâÂúñÂ±§ (groupLayers)
     * 3. Ê¢ù‰ª∂ÂåπÈÖçÔºöÊ™¢Êü• layerId ÊòØÂê¶ËàáÁõÆÊ®ô ID ÂÆåÂÖ®ÂåπÈÖç
     * 4. ÁµêÊûúËøîÂõûÔºöÊâæÂà∞ÂâáÁ´ãÂç≥ËøîÂõûÔºåÊú™ÊâæÂà∞ÂâáËøîÂõû null
     *
     * üìä ÊÄßËÉΩÂàÜÊûêÔºö
     * - ÊôÇÈñìË§áÈõúÂ∫¶ÔºöO(n*m)ÔºåÂÖ∂‰∏≠ n ÊòØÁµÑÊï∏Ôºåm ÊòØÂπ≥ÂùáÊØèÁµÑÁöÑÂúñÂ±§Êï∏
     * - Á©∫ÈñìË§áÈõúÂ∫¶ÔºöO(1)Ôºå‰∏çÈúÄË¶ÅÈ°çÂ§ñÁöÑÂ≠òÂÑ≤Á©∫Èñì
     * - ÊúÄ‰Ω≥ÊÉÖÊ≥ÅÔºöO(1)ÔºåÁ¨¨‰∏ÄÂÄãÂúñÂ±§Â∞±ÊòØÁõÆÊ®ô
     * - ÊúÄÂ£ûÊÉÖÊ≥ÅÔºöO(n*m)ÔºåÈúÄË¶ÅÈÅçÊ≠∑ÊâÄÊúâÂúñÂ±§
     *
     * @param {string} layerId - Ë¶ÅÊü•ÊâæÁöÑÂúñÂ±§ÂîØ‰∏ÄÊ®ôË≠òÁ¨¶ÔºåÂøÖÈ†àÊòØÁ≤æÁ¢∫ÂåπÈÖç
     * @returns {Object|null} ÊâæÂà∞ÁöÑÂúñÂ±§Â∞çË±°ÔºåÂåÖÂê´ÊâÄÊúâÈÖçÁΩÆ‰ø°ÊÅØÔºõÂ¶ÇÊûúÊú™ÊâæÂà∞ÂâáËøîÂõû null
     *
     * @example
     * // Êü•ÊâæÂè∞ÁÅ£ÂúñÂ±§
     * const taiwanLayer = findLayerById('taiwan');
     * if (taiwanLayer) {
     *   console.log('ÊâæÂà∞ÂúñÂ±§:', taiwanLayer.layerName); // Ëº∏Âá∫: "ÊâæÂà∞ÂúñÂ±§: TAIWAN"
     *   console.log('‰∏≠ÂøÉÂ∫ßÊ®ô:', taiwanLayer.center);     // Ëº∏Âá∫: [25.0457, 121.5196]
     * }
     *
     * // Êü•Êâæ‰∏çÂ≠òÂú®ÁöÑÂúñÂ±§
     * const nonExistentLayer = findLayerById('non-existent');
     * if (!nonExistentLayer) {
     *   console.log('ÂúñÂ±§‰∏çÂ≠òÂú®'); // Ëº∏Âá∫: "ÂúñÂ±§‰∏çÂ≠òÂú®"
     * }
     *
     * @since 1.0.0
     * @author 30 Day Map Challenge Team
     */
    const findLayerById = (layerId) => {
      // üîç Â§ñÂ±§Âæ™Áí∞ÔºöÈÅçÊ≠∑ÊâÄÊúâÂúñÂ±§ÁµÑÔºåÁ¢∫‰øùÊêúÁ¥¢Ë¶ÜËìãÊâÄÊúâÂèØËÉΩÁöÑÂúñÂ±§
      for (const group of layers.value) {
        // üîç ÂÖßÂ±§Âæ™Áí∞ÔºöÈÅçÊ≠∑Áï∂ÂâçÁµÑÁöÑÊâÄÊúâÂúñÂ±§ÔºåÈÄ≤Ë°åÁ≤æÁ¢∫ÂåπÈÖç
        for (const layer of group.groupLayers) {
          // ‚úÖ Ê¢ù‰ª∂ÂåπÈÖçÔºöÊ™¢Êü•ÂúñÂ±§ ID ÊòØÂê¶ËàáÁõÆÊ®ô ID ÂÆåÂÖ®ÂåπÈÖç
          if (layer.layerId === layerId) {
            return layer; // üéØ Á´ãÂç≥ËøîÂõûÊâæÂà∞ÁöÑÂúñÂ±§Â∞çË±°ÔºåÂåÖÂê´ÊâÄÊúâÈÖçÁΩÆ‰ø°ÊÅØ
          }
        }
      }
      return null; // ‚ùå Êú™ÊâæÂà∞ÊåáÂÆö ID ÁöÑÂúñÂ±§ÔºåËøîÂõû null Ë°®Á§∫ÊêúÁ¥¢Â§±Êïó
    };

    /**
     * üìã Áç≤ÂèñÊâÄÊúâÂúñÂ±§ÁöÑÊâÅÂπ≥Èô£Âàó (Get All Layers as Flat Array)
     *
     * ÈÄôÊòØ‰∏ÄÂÄãÊï∏ÊìöËΩâÊèõÊñπÊ≥ïÔºåÂ∞áÂàÜÁµÑÁµêÊßãÁöÑÂúñÂ±§ÈÖçÁΩÆËΩâÊèõÁÇ∫ÊâÅÂπ≥ÁöÑ‰∏ÄÁ∂≠Èô£Âàó„ÄÇ
     * ÈÄôÁ®ÆËΩâÊèõ‰ΩøÂæóÊï∏ÊìöÊõ¥ÂÆπÊòìÈÄ≤Ë°åÊâπÈáèÊìç‰Ωú„ÄÅÈÅçÊ≠∑ÂíåËôïÁêÜÔºåÁâπÂà•ÈÅ©ÂêàÂú®
     * UI ÁµÑ‰ª∂‰∏≠ÈÄ≤Ë°åÂàóË°®Ê∏≤ÊüìÂíåÊï∏ÊìöÂ±ïÁ§∫„ÄÇ
     *
     * üéØ ÂäüËÉΩÁâπÊÄßÔºö
     * - ÊâÅÂπ≥ÂåñËΩâÊèõÔºöÂ∞áÂµåÂ•óÁöÑÂàÜÁµÑÁµêÊßãËΩâÊèõÁÇ∫‰∏ÄÁ∂≠Èô£Âàó
     * - ‰øùÊåÅÂÆåÊï¥ÊÄßÔºö‰øùÁïôÊâÄÊúâÂúñÂ±§ÁöÑÂéüÂßãÂ±¨ÊÄßÂíåÈÖçÁΩÆ‰ø°ÊÅØ
     * - È´òÊïàËôïÁêÜÔºö‰ΩøÁî®Â±ïÈñãÈÅãÁÆóÁ¨¶ÂØ¶ÁèæÈ´òÊïàÁöÑÊï∏ÁµÑÂêà‰Ωµ
     * - ÈüøÊáâÂºèÊõ¥Êñ∞ÔºöÁï∂ÂéüÂßãÊï∏ÊìöËÆäÊõ¥ÊôÇÔºåËøîÂõûÁöÑÈô£ÂàóÊúÉËá™ÂãïÊõ¥Êñ∞
     *
     * üîÑ ËΩâÊèõÈÅéÁ®ãÔºö
     * 1. ÂàùÂßãÂåñÔºöÂâµÂª∫Á©∫Èô£Âàó‰ΩúÁÇ∫ÁµêÊûúÂÆπÂô®
     * 2. ÈÅçÊ≠∑ÂàÜÁµÑÔºöÂæ™Áí∞ËôïÁêÜÊØèÂÄãÂúñÂ±§ÁµÑ
     * 3. Â±ïÈñãÂêà‰ΩµÔºö‰ΩøÁî®Â±ïÈñãÈÅãÁÆóÁ¨¶Â∞áÊØèÁµÑÁöÑÂúñÂ±§Ê∑ªÂä†Âà∞ÁµêÊûúÈô£Âàó
     * 4. ËøîÂõûÁµêÊûúÔºöËøîÂõûÂåÖÂê´ÊâÄÊúâÂúñÂ±§ÁöÑÊâÅÂπ≥Èô£Âàó
     *
     * üìä ÊÄßËÉΩÂàÜÊûêÔºö
     * - ÊôÇÈñìË§áÈõúÂ∫¶ÔºöO(n)ÔºåÂÖ∂‰∏≠ n ÊòØÊâÄÊúâÂúñÂ±§ÁöÑÁ∏ΩÊï∏
     * - Á©∫ÈñìË§áÈõúÂ∫¶ÔºöO(n)ÔºåÈúÄË¶ÅÂâµÂª∫Êñ∞ÁöÑÈô£ÂàóÂ≠òÂÑ≤ÁµêÊûú
     * - Ë®òÊÜ∂È´îÊïàÁéáÔºö‰ΩøÁî®Â±ïÈñãÈÅãÁÆóÁ¨¶ÔºåÈÅøÂÖçÂ§öÊ¨°Êï∏ÁµÑÊìç‰Ωú
     * - ÈüøÊáâÂºèÈñãÈä∑ÔºöÊØèÊ¨°Ë™øÁî®ÈÉΩÊúÉÈáçÊñ∞Ë®àÁÆóÔºå‰ΩÜ Vue ÊúÉËá™ÂãïÂÑ™Âåñ
     *
     * üé® ‰ΩøÁî®Â†¥ÊôØÔºö
     * - UI ÂàóË°®Ê∏≤ÊüìÔºöÂú® Vue Ê®°Êùø‰∏≠ÈÄ≤Ë°å v-for Âæ™Áí∞
     * - Êï∏ÊìöÁµ±Ë®àÔºöË®àÁÆóÂúñÂ±§Á∏ΩÊï∏„ÄÅÂèØË¶ãÂúñÂ±§Êï∏ÈáèÁ≠â
     * - ÊâπÈáèÊìç‰ΩúÔºöÂ∞çÊâÄÊúâÂúñÂ±§ÈÄ≤Ë°åÁµ±‰∏ÄÁöÑËôïÁêÜ
     * - Êï∏ÊìöÈÅéÊøæÔºöÂü∫ÊñºÊ¢ù‰ª∂ÁØ©ÈÅ∏ÁâπÂÆöÁöÑÂúñÂ±§
     *
     * @returns {Array} ÂåÖÂê´ÊâÄÊúâÂúñÂ±§ÁöÑÊâÅÂπ≥Èô£ÂàóÔºåÊØèÂÄãÂÖÉÁ¥†ÈÉΩÊòØÂÆåÊï¥ÁöÑÂúñÂ±§ÈÖçÁΩÆÂ∞çË±°
     *
     * @example
     * // Áç≤ÂèñÊâÄÊúâÂúñÂ±§‰∏¶ÈÄ≤Ë°åÁµ±Ë®à
     * const allLayers = getAllLayers();
     * console.log('Á∏ΩÂÖ±Êúâ', allLayers.length, 'ÂÄãÂúñÂ±§'); // Ëº∏Âá∫: "Á∏ΩÂÖ±Êúâ 6 ÂÄãÂúñÂ±§"
     *
     * // Âú® Vue Ê®°Êùø‰∏≠‰ΩøÁî®
     * // <div v-for="layer in getAllLayers()" :key="layer.layerId">
     * //   {{ layer.layerName }}
     * // </div>
     *
     * // ÈÄ≤Ë°åÊï∏ÊìöÈÅéÊøæ
     * const asianCountries = getAllLayers().filter(layer =>
     *   ['taiwan', 'china', 'japan'].includes(layer.layerId)
     * );
     *
     * @since 1.0.0
     * @author 30 Day Map Challenge Team
     */
    const getAllLayers = () => {
      const allLayers = []; // üèóÔ∏è ÂàùÂßãÂåñÁµêÊûúÈô£ÂàóÔºåÁî®ÊñºÂ≠òÂÑ≤ÊâÅÂπ≥ÂåñÁöÑÂúñÂ±§Êï∏Êìö

      // üîÑ ÈÅçÊ≠∑ÊâÄÊúâÂúñÂ±§ÁµÑÔºåÁ¢∫‰øùËôïÁêÜÊØèÂÄãÂàÜÁµÑ‰∏≠ÁöÑÊâÄÊúâÂúñÂ±§
      for (const group of layers.value) {
        // üì¶ ‰ΩøÁî®Â±ïÈñãÈÅãÁÆóÁ¨¶Â∞áÁï∂ÂâçÁµÑÁöÑÊâÄÊúâÂúñÂ±§Ê∑ªÂä†Âà∞ÁµêÊûúÈô£Âàó
        // ÈÄôÁ®ÆÊñπÂºèÊØî‰ΩøÁî® concat Êõ¥È´òÊïàÔºå‰∏î‰ª£Á¢ºÊõ¥Á∞°ÊΩî
        allLayers.push(...group.groupLayers);
      }

      return allLayers; // üéØ ËøîÂõûÊâÅÂπ≥ÂåñÁöÑÂúñÂ±§Èô£ÂàóÔºåÂåÖÂê´ÊâÄÊúâÂúñÂ±§ÁöÑÂÆåÊï¥ÈÖçÁΩÆ‰ø°ÊÅØ
    };

    /**
     * üîÑ ÂàáÊèõÂúñÂ±§ÂèØË¶ãÊÄßÁãÄÊÖã (Toggle Layer Visibility)
     *
     * ÊéßÂà∂ÊåáÂÆöÂúñÂ±§ÁöÑÈ°ØÁ§∫/Èö±ËóèÁãÄÊÖãÔºå‰∏¶Âú®ÂúñÂ±§È¶ñÊ¨°È°ØÁ§∫ÊôÇËá™ÂãïËºâÂÖ•Áõ∏ÈóúÊï∏Êìö„ÄÇ
     * ÈÄôÊòØÂúñÂ±§ÁÆ°ÁêÜÁöÑÊ†∏ÂøÉÊñπÊ≥ïÔºåË≤†Ë≤¨ËôïÁêÜÂúñÂ±§ÁãÄÊÖãËÆäÊõ¥ÂíåÊï∏ÊìöËºâÂÖ•ÈÇèËºØ„ÄÇ
     *
     * @param {string} layerId - Ë¶ÅÂàáÊèõÁãÄÊÖãÁöÑÂúñÂ±§ÂîØ‰∏ÄÊ®ôË≠òÁ¨¶
     * @returns {Promise<void>} Áï∞Ê≠•Êìç‰ΩúÔºåÁÑ°ËøîÂõûÂÄº
     *
     * @example
     * // ÂàáÊèõÂÆâÈ§äÊ©üÊßãÂúñÂ±§ÁöÑÈ°ØÁ§∫ÁãÄÊÖã
     * await toggleLayerVisibility('ÂÆâÈ§äÊ©üÊßã');
     */
    // ÁßªÈô§ÂúñÂ±§ÂèØË¶ãÊÄßÂàáÊèõÔºàÂüéÂ∏ÇÂúñÂ±§Ê∞∏‰πÖÂèØË¶ãÔºå‰∏îÁÑ°ÈúÄÂãæÈÅ∏ÂàáÊèõÔºâ

    // ÁßªÈô§ GeoJSON ËºâÂÖ•ÂäüËÉΩÔºåÁèæÂú®Áõ¥Êé•‰ΩøÁî®Â∫ßÊ®ôÈªû

    // ------------------------------------------------------------
    // ÈÅ∏‰∏≠ÁöÑÂú∞ÂúñÁâ©‰ª∂ÔºàÁî®ÊñºÊ∏ÖÈô§ÈÅ∏ÂèñÁãÄÊÖãÔºâ
    const selectedFeature = ref(null);

    const setSelectedFeature = (feature) => {
      selectedFeature.value = feature;
    };

    // ------------------------------------------------------------
    // Âú∞ÂúñÂ∞éËà™ÂäüËÉΩ
    const mapInstance = ref(null);

    const setMapInstance = (map) => {
      mapInstance.value = map;
    };

    /**
     * üåç Â∞éËà™Âà∞ÊåáÂÆöÂúãÂÆ∂ (Navigate to Specified Country)
     *
     * ÈÄôÊòØÂú∞ÂúñÂ∞éËà™ÁöÑÊ†∏ÂøÉÊñπÊ≥ïÔºåË≤†Ë≤¨Â∞áÂú∞ÂúñË¶ñÂúñÁßªÂãïÂà∞ÊåáÂÆöÂúãÂÆ∂ÁöÑ‰∏≠ÂøÉ‰ΩçÁΩÆ„ÄÇ
     * Êú¨ÊñπÊ≥ïÊï¥Âêà‰∫ÜÂúñÂ±§Êü•Êâæ„ÄÅÂú∞ÂúñÂØ¶‰æãÊ™¢Êü•„ÄÅÂ∫ßÊ®ôËΩâÊèõÂíåÂ∞éËà™Âü∑Ë°åÁ≠âÂÆåÊï¥ÊµÅÁ®ãÔºå
     * Êèê‰æõÁ©©ÂÆöÂèØÈù†ÁöÑÂú∞ÂúñÂ∞éËà™ÂäüËÉΩ„ÄÇ
     *
     * üéØ ÂäüËÉΩÁâπÊÄßÔºö
     * - Êô∫ËÉΩÊü•ÊâæÔºöËá™ÂãïÊü•ÊâæÊåáÂÆöÂúãÂÆ∂ÁöÑÂúñÂ±§ÈÖçÁΩÆ‰ø°ÊÅØ
     * - ÁãÄÊÖãÊ™¢Êü•ÔºöÈ©óË≠âÂú∞ÂúñÂØ¶‰æãÊòØÂê¶Ê∫ñÂÇôÂ∞±Á∑í
     * - ÈáçË©¶Ê©üÂà∂ÔºöÁï∂Âú∞ÂúñÊú™Ê∫ñÂÇôÂ•ΩÊôÇÔºåËá™ÂãïÂª∂ÈÅ≤ÈáçË©¶
     * - Â∫ßÊ®ôËΩâÊèõÔºöÂ∞áÂúñÂ±§Â∫ßÊ®ôËΩâÊèõÁÇ∫ Leaflet Ê†ºÂºè
     * - ÈåØË™§ËôïÁêÜÔºöÂÆåÊï¥ÁöÑÈåØË™§ÊçïÁç≤ÂíåÊó•Ë™åË®òÈåÑ
     * - ÂãïÁï´ÊéßÂà∂ÔºöÊîØÊè¥Âπ≥ÊªëÊàñÁû¨ÊôÇÁöÑÂú∞ÂúñÁßªÂãï
     *
     * üîÑ Âü∑Ë°åÊµÅÁ®ãÔºö
     * 1. ÂúñÂ±§Êü•ÊâæÔºö‰ΩøÁî® findLayerById Êü•ÊâæÁõÆÊ®ôÂúãÂÆ∂ÂúñÂ±§
     * 2. ÁãÄÊÖãÈ©óË≠âÔºöÊ™¢Êü•Âú∞ÂúñÂØ¶‰æãÊòØÂê¶Â∑≤ÂàùÂßãÂåñ
     * 3. ÈáçË©¶ËôïÁêÜÔºöÂ¶ÇÊûúÂú∞ÂúñÊú™Ê∫ñÂÇôÂ•ΩÔºåÂª∂ÈÅ≤ 1 ÁßíÂæåÈáçË©¶
     * 4. Â∫ßÊ®ôÊèêÂèñÔºöÂæûÂúñÂ±§ÈÖçÁΩÆ‰∏≠ÊèêÂèñ‰∏≠ÂøÉÂ∫ßÊ®ô
     * 5. Â∞éËà™Âü∑Ë°åÔºöË™øÁî® Leaflet ÁöÑ setView ÊñπÊ≥ïÂü∑Ë°åÂ∞éËà™
     * 6. ÁµêÊûúË®òÈåÑÔºöË®òÈåÑÂ∞éËà™ÊàêÂäüÊàñÂ§±ÊïóÁöÑË©≥Á¥∞‰ø°ÊÅØ
     *
     * üõ°Ô∏è ÈåØË™§ËôïÁêÜÔºö
     * - ÂúñÂ±§‰∏çÂ≠òÂú®ÔºöË®òÈåÑÈåØË™§‰∏¶ÊèêÂâçËøîÂõû
     * - Âú∞ÂúñÊú™Ê∫ñÂÇôÔºöËá™ÂãïÈáçË©¶Ê©üÂà∂ÔºåÊúÄÂ§öÁ≠âÂæÖ 1 Áßí
     * - Â∞éËà™Â§±ÊïóÔºöÊçïÁç≤Áï∞Â∏∏‰∏¶Ë®òÈåÑË©≥Á¥∞ÈåØË™§‰ø°ÊÅØ
     * - ÂèÉÊï∏È©óË≠âÔºöÁ¢∫‰øù countryId ÂèÉÊï∏ÁöÑÊúâÊïàÊÄß
     *
     * üìä ÊÄßËÉΩÂÑ™ÂåñÔºö
     * - Âø´ÈÄüÂ§±ÊïóÔºöÁÑ°ÊïàÂèÉÊï∏ÊôÇÁ´ãÂç≥ËøîÂõûÔºåÈÅøÂÖç‰∏çÂøÖË¶ÅÁöÑËôïÁêÜ
     * - Âª∂ÈÅ≤ÈáçË©¶ÔºöÈÅøÂÖçÈ†ªÁπÅÁöÑÂêåÊ≠•Ê™¢Êü•ÔºåÊ∏õÂ∞ë CPU ‰ΩøÁî®
     * - ÂãïÁï´ÊéßÂà∂ÔºöÁ¶ÅÁî®ÂãïÁï´‰ª•ÊèêÂçáÂ∞éËà™ÈÄüÂ∫¶
     * - Ë®òÊÜ∂È´îÁÆ°ÁêÜÔºöÈÅøÂÖçÂâµÂª∫‰∏çÂøÖË¶ÅÁöÑËá®ÊôÇÂ∞çË±°
     *
     * @param {string} countryId - ÂúãÂÆ∂ÂúñÂ±§ÁöÑÂîØ‰∏ÄÊ®ôË≠òÁ¨¶ÔºåÂøÖÈ†àÊòØÊúâÊïàÁöÑÂúñÂ±§ ID
     * @returns {void} ÁÑ°ËøîÂõûÂÄºÔºå‰ΩÜÊúÉÂü∑Ë°åÂú∞ÂúñÂ∞éËà™Êìç‰Ωú
     *
     * @example
     * // Â∞éËà™Âà∞Âè∞ÁÅ£
     * navigateToCountry('taiwan');
     *
     * // Â∞éËà™Âà∞Êó•Êú¨
     * navigateToCountry('japan');
     *
     * // Â∞éËà™Âà∞‰∏çÂ≠òÂú®ÁöÑÂúãÂÆ∂ÔºàÊúÉË®òÈåÑÈåØË™§Ôºâ
     * navigateToCountry('non-existent');
     *
     * @throws {Error} Áï∂Âú∞ÂúñÂØ¶‰æãÁÑ°ÊïàÊàñÂ∞éËà™Â§±ÊïóÊôÇÊúÉÊããÂá∫ÈåØË™§
     * @since 1.0.0
     * @author 30 Day Map Challenge Team
     */
    const navigateToCountry = (countryId) => {
      // üîç Á¨¨‰∏ÄÊ≠•ÔºöÊü•ÊâæÁõÆÊ®ôÂúãÂÆ∂ÂúñÂ±§ÔºåÁ¢∫‰øùÂúñÂ±§ÈÖçÁΩÆÂ≠òÂú®
      const countryLayer = findLayerById(countryId);
      if (!countryLayer) {
        console.error('‚ùå Êâæ‰∏çÂà∞ÂúãÂÆ∂ÂúñÂ±§:', countryId);
        return; // üö´ ÊèêÂâçËøîÂõûÔºåÈÅøÂÖçÂæåÁ∫åÁÑ°ÊïàÊìç‰Ωú
      }

      // üîç Á¨¨‰∫åÊ≠•ÔºöÊ™¢Êü•Âú∞ÂúñÂØ¶‰æãÊòØÂê¶Ê∫ñÂÇôÂ∞±Á∑íÔºåÁ¢∫‰øùÂú∞ÂúñÂ∑≤ÂàùÂßãÂåñ
      if (!mapInstance.value) {
        console.error('‚ùå Âú∞ÂúñÂØ¶‰æãÊú™Ê∫ñÂÇôÂ∞±Á∑íÔºåÁ≠âÂæÖÂú∞ÂúñÂàùÂßãÂåñ...');

        // üîÑ Âª∂ÈÅ≤ÈáçË©¶Ê©üÂà∂ÔºöÁ≠âÂæÖ 1 ÁßíÂæåÈáçÊñ∞ÂòóË©¶Â∞éËà™
        setTimeout(() => {
          if (mapInstance.value) {
            console.log('üåç Âú∞ÂúñÂ∑≤Ê∫ñÂÇôÂ∞±Á∑íÔºåÈáçÊñ∞ÂòóË©¶ÁßªÂãï');
            navigateToCountry(countryId); // üîÑ ÈÅûÊ≠∏Ë™øÁî®ÔºåÈáçÊñ∞Âü∑Ë°åÂ∞éËà™
          } else {
            console.error('‚ùå Âú∞ÂúñÂØ¶‰æã‰ªçÊú™Ê∫ñÂÇôÂ∞±Á∑í');
          }
        }, 1000);
        return; // üö´ ÊèêÂâçËøîÂõûÔºåÁ≠âÂæÖÈáçË©¶
      }

      // üìç Á¨¨‰∏âÊ≠•ÔºöÊèêÂèñÂúãÂÆ∂‰∏≠ÂøÉÂ∫ßÊ®ôÔºåÊ∫ñÂÇôÂ∞éËà™ÂèÉÊï∏
      const [lat, lng] = countryLayer.center;
      const targetCenter = [lat, lng]; // üó∫Ô∏è Leaflet ÈúÄË¶Å [Á∑ØÂ∫¶, Á∂ìÂ∫¶] Ê†ºÂºè
      const optimalZoom = FIXED_ZOOM_LEVEL; // üéØ ‰ΩøÁî®Âõ∫ÂÆöÁöÑÁ∏ÆÊîæÁ¥öÂà•

      // üöÄ Á¨¨ÂõõÊ≠•ÔºöÂü∑Ë°åÂú∞ÂúñÂ∞éËà™ÔºåÁßªÂãïÂà∞ÁõÆÊ®ô‰ΩçÁΩÆ
      try {
        mapInstance.value.setView(targetCenter, optimalZoom, { animate: false });
        console.log(`üåç ÊàêÂäüÂ∞éËà™Âà∞ÂúãÂÆ∂: ${countryLayer.layerName}ÔºåÁ∏ÆÊîæÁ≠âÁ¥ö: ${optimalZoom}`);
      } catch (error) {
        console.error('‚ùå Âú∞ÂúñÂ∞éËà™Â§±Êïó:', error);
      }
    };

    return {
      layers,
      findLayerById, // Ê†πÊìö ID Â∞ãÊâæÂúñÂ±§
      getAllLayers, // Áç≤ÂèñÊâÄÊúâÂúñÂ±§ÁöÑÊâÅÂπ≥Èô£Âàó
      selectedFeature, // ÈÅ∏‰∏≠ÁöÑÂú∞ÂúñË¶ÅÁ¥†
      setSelectedFeature, // Ë®≠ÂÆöÈÅ∏‰∏≠ÁöÑÂú∞ÂúñË¶ÅÁ¥†
      mapInstance, // Âú∞ÂúñÂØ¶‰æã
      setMapInstance, // Ë®≠ÂÆöÂú∞ÂúñÂØ¶‰æã
      navigateToCountry, // Â∞éËà™Âà∞ÊåáÂÆöÂúãÂÆ∂
      // ÊâÄÊúâÂúñÂ±§ÈÉΩÊòØÂèØË¶ãÁöÑÔºåÊâÄ‰ª•Áõ¥Êé•ËøîÂõûÊâÄÊúâÂúñÂ±§
      visibleLayers: computed(() => getAllLayers()),
      FIXED_ZOOM_LEVEL, // Âõ∫ÂÆöÁ∏ÆÊîæÁ¥öÂà•Â∏∏Êï∏
    };
  },
  {
    persist: true,
  }
);
